import React, {useEffect, useRef, useState} from 'react';

import styles from "./AppDevelopmentDesktopApps.module.scss"
import {
    aboutDesktopAppsHeader,
    aboutDesktopAppsMain, aboutDesktopAppsPlaybar, aboutDesktopAppsPlaybarActive,
    aboutDesktopAppsSide,
    aboutDesktopAppsSinger
} from "../../../assets/images";
import Svg from "../../layout/Svg/Svg";
import {musicNextIcon, musicPlayIcon, musicPrevIcon, musicStopIcon} from "../../../assets/svg";
import desktopAppsMusic from "../../../assets/audios/emma petress.mp3"

function AppDevelopmentDesktopApps() {
    const [progressBarInnerWidth,setProgressBarInnerWidth] = useState(0)
    const [isPlaying,setIsPlaying] = useState(false)
    const [playPercentage,setPlayPercentage] = useState(0)
    const [currTime,setCurrTime] = useState('0:00')
    const [duration,setDuration] = useState('0:00')
    const playBarRef = useRef()
    const audioRef = useRef()

    useEffect(() => {
        window.addEventListener('resize',changePlayBarInnerWidth)

        return () => {
            window.removeEventListener('resize',changePlayBarInnerWidth)
        }
    },[])

    useEffect(() => {
        isPlaying ?  audioRef.current.play() : audioRef.current.pause()
    },[isPlaying])

    const changePlayBarInnerWidth = () => {
        setProgressBarInnerWidth(playBarRef.current.getBoundingClientRect().width)
    }
    const onPlayMusic = () => {
        setIsPlaying(preevState => !preevState)
    }
    const onCanPlay = (e) => {
        const min = Math.floor(e.target.duration / 60);
        const sec = Math.floor(e.target.duration % 60);
        setDuration(min + ':' + sec)
    }

    const onTimeUpdate = (e) => {
        const min = Math.floor(e.target.currentTime / 60)
        const sec = Math.floor(e.target.currentTime % 60).toString().padStart(2,'0');
        setCurrTime(min + ':' + sec)

        setPlayPercentage((e.target.currentTime * 100) / e.target.duration)
    }

    const onEnded = () => {
        setIsPlaying(false)
    }
    return (
        <section className={`${styles['appDevelopmentDesktopApps']} container`}>
            <div className={styles["appDevelopmentDesktopApps__imgBlock"]}>
                <img src={aboutDesktopAppsHeader} alt="header"
                     className={styles["appDevelopmentDesktopApps__headerImg"]}/>
                <div className={styles["appDevelopmentDesktopApps__imgMainBlock"]}>
                    <img src={aboutDesktopAppsMain} alt="Desktop App"
                         className={styles["appDevelopmentDesktopApps__mainImg"]}/>
                    <div className={styles["appDevelopmentDesktopApps__aside"]}>
                        <img src={aboutDesktopAppsSinger} alt="Singer"
                             className={styles["appDevelopmentDesktopApps__singerImg"]}/>
                        <div className={styles["appDevelopmentDesktopApps__player"]}>
                            <audio
                                src={desktopAppsMusic}
                                ref={audioRef}
                                onTimeUpdate={onTimeUpdate}
                                onCanPlay={onCanPlay}
                                onEnded={onEnded}
                            ></audio>
                            <div className={styles["appDevelopmentDesktopApps__playBarBlock"]}>
                                <p className={styles["appDevelopmentDesktopApps__playerTime"]}>{currTime}</p>
                                <div className={styles["appDevelopmentDesktopApps__playBar"]}>
                                    <img
                                        ref={playBarRef}
                                        onLoad={changePlayBarInnerWidth}
                                        src={aboutDesktopAppsPlaybar}
                                        alt="playbar"
                                        className={styles["appDevelopmentDesktopApps__playBarIcon"]}
                                    />
                                    <div className={styles["appDevelopmentDesktopApps__playBarInner"]} style={{width: `${playPercentage}%`}}>
                                        <img
                                            src={aboutDesktopAppsPlaybarActive}
                                            alt="playbar"
                                            className={styles["appDevelopmentDesktopApps__playBarIconInner"]}
                                            style={{width: progressBarInnerWidth}}
                                        />
                                    </div>
                                </div>
                                <p className={styles["appDevelopmentDesktopApps__playerTime"]}>{duration}</p>
                            </div>
                            <div className={styles["appDevelopmentDesktopApps__playerControls"]}>
                                <Svg
                                    className={styles["appDevelopmentDesktopApps__playerControlIcon"]}
                                    id={musicPrevIcon}
                                />
                                <button className={styles["appDevelopmentDesktopApps__playerBtn"]} onClick={onPlayMusic}>
                                    <Svg
                                        className={styles["appDevelopmentDesktopApps__playerControlIcon"]}
                                        id={isPlaying ? musicStopIcon : musicPlayIcon}
                                    />
                                </button>
                                <Svg
                                    className={styles["appDevelopmentDesktopApps__playerControlIcon"]}
                                    id={musicNextIcon}
                                />
                            </div>
                        </div>
                        <img src={aboutDesktopAppsSide} alt="Music List"
                             className={styles["appDevelopmentDesktopApps__sideImg"]}/>
                    </div>
                </div>
            </div>
            <div className={styles["appDevelopmentDesktopApps__textBlock"]}>
                <h3 className={`${styles["appDevelopmentDesktopApps__title"]} titleDef`}><span
                    className="lightBlueText">Desktop </span> App Development</h3>
                <p className={`${styles["appDevelopmentDesktopApps__contentText"]} contentTextDef`}>
                    With the ability to work offline, desktop applications enable faster processing and seamless access to data, providing dependable functionality with fewer crashes and greater control over data confidentiality.
                </p>
                <p className={`${styles["appDevelopmentDesktopApps__contentText"]} contentTextDef`}>
                    By employing a modular approach, we engineer desktop client-server applications that excel at proficiently  handling substantial data volumes and intricate business logic.
                </p>
                <p className={`${styles["appDevelopmentDesktopApps__contentText"]} contentTextDef`}>Placing a special emphasis on the exceptional capabilities of C++, our team of specialists has a successful track record in developing complex desktop applications. We leverage technologies for diverse platforms, encompassing Windows, macOS, and Linux.
                </p>
                <p className={`${styles["appDevelopmentDesktopApps__contentText"]} contentTextDef`}>
                    Significant performance improvements are also achieved by utilizing hardware acceleration or optimizing the interaction between various system components, such as the processor, motherboard, and graphics processor.
                </p>
            </div>
        </section>
    );
}

export default AppDevelopmentDesktopApps;